import { Field } from "redux-form";
import style from "./FormsControl.module.css";

const FormsControl = ({visited, error, pristine, active, children}) => {
    const someError =
        (visited && error && !pristine) ||
        (visited && error && !active);
    return (
        <div
            className={`${style.formsControl} ${someError ? style.error : ""}`}
        >
            <span>{someError && error}</span>
            {children}
        </div>
    );
};

export const Textarea = ({ input, meta, ...props }) => {
    return (
        <FormsControl {...meta}>
            <textarea {...input} {...props} />
        </FormsControl>
    );
};

export const Input = ({ input, meta, ...props }) => {
    return (
        <FormsControl {...meta}>
            <input {...input} {...props} />
        </FormsControl>
    );
};

export const CreateField = (props, divSyle = "", text = "" ) => (
    <div className={divSyle}>
        <label>{text}
            <Field
                {...props}
            />
        </label>
    </div>
);
