import style from "./Content.module.css";
import Nav from "../Navbar/Nav";
import { Route } from "react-router-dom";
import React, { lazy } from "react";
import NewsPage from "../NewsPage/NewsPage";
import MusicPage from "../MusicPage/MusicPage";
import SettingPage from "../SettingPage/SettingPage";
import ProfilePageContainer from "../ProfilePage/ProfilePageContainer";
import WithSuspense from './../../hoc/WithSuspense';
const MessagePage = lazy(() => import("../MessagePage/MessagePage"));
const UserSearchPageContainer = lazy(() =>
    import("../UserSearchPage/UserSearchPageContainer")
);

const Content = () => {
    return (
        <div className={style.content}>
            <Nav />
            <div className={style.app_content}>
                <Route
                    path="/profile/:userId?"
                    render={() => <ProfilePageContainer />}
                />
                <Route
                    path="/dialogs"
                    render={() => <WithSuspense Component={MessagePage}/>}
                />
                <Route
                    path="/user_search"
                    render={() => <WithSuspense Component={UserSearchPageContainer}/>}
                />
                <Route path="/news" render={() => <NewsPage />} />
                <Route path="/musics" render={() => <MusicPage />} />
                <Route path="/settings" render={() => <SettingPage />} />
            </div>
        </div>
    );
};

export default Content;
