import style from "./Login.module.css";
import { reduxForm } from 'redux-form';
import { CreateField, Input } from "../common/formsControl/FormsControl";
import { required } from "../utilits/validation/Validate";
import { connect } from "react-redux";
import { login } from "../../Redux/auth-reducer";
import { Redirect } from "react-router";

function Login({ login, isAuth }) {
    const onSubmit = (formData) => { login(formData.email, formData.password, formData.rememberMe); };
    return isAuth ? (
        <Redirect to="/profile" />
    ):(
            <div className={style.login}>
                <div className={style.content}>
                    <div className={style.text}>Sign In</div>
                    <LoginReduxForm onSubmit={onSubmit} />
                </div>
            </div>
    )
}


function LoginForm({ handleSubmit, error }) {
    return (
        <form onSubmit={handleSubmit}>
            {CreateField({ name: 'email', type: 'text', span: style.span, placeholder: 'Email', component: Input, validate: [required] }, style.field)}
            {CreateField({ name: 'password', type: 'password', span: style.span, placeholder: 'Password', component: Input, validate: [required] }, style.field)}
            {CreateField({ className: style.rememberMe, name: 'rememberMe', type: 'checkbox', component: 'input' }, null, "Remember me:")}

            <div className={style.formError}>
                {error}
            </div>
            <button className={style.button}>Sign In</button>
        </form>
    );
}

let LoginReduxForm = reduxForm({ form: 'login' })(LoginForm)

export default connect(null, { login: login })(Login);