import style from "./ProfileInfo.module.css";
import userImage from "./../../../img/user.png";
import Status from "./Status/Status";
import React from "react";

const ProfileInfo = React.memo(({ profile, uppdateUserStatus, status }) => {
    return (
        <div className={style.my_value}>
            <img
                src={!profile.photos.large ? userImage : profile.photos.large}
                alt="#"
            />
            <div>
                <h1>{profile.fullName}</h1>
                <Status status={status} uppdateUserStatus={uppdateUserStatus} />
            </div>
        </div>
    );
});

export default ProfileInfo;
