import Post from "./Posts/Post";
import style from "./UserPosts.module.css";
import React from "react";
import { reduxForm } from "redux-form";
import { CreateField, Textarea } from "../../common/formsControl/FormsControl";
import { maxLength, required } from "../../utilits/validation/Validate";

const maxLength100 = maxLength(100);

const UserPosts = React.memo((props) => {

    let addNewPost = (value) => {
        props.addNewPost(value.newPostText);
    };

    return (
        <div className={style.myPosts}>
            <div className={style.newPost}>
                <h3>New posts</h3>
                <NewPostReduxForm onSubmit={addNewPost} />
            </div>
            <h2>Posts</h2>
            {[...props.userPosts].reverse().map((post) => (<Post text={post.text} key={post.id} />))}
        </div>
    );
});

const NewPostForm = ({handleSubmit}) => {
    return (
        <form onSubmit={handleSubmit}>
            {CreateField({name:"newPostText", placeholder:"Your news", component:Textarea, validate:[required, maxLength100]})}
            <div className={style.button}>
                <button className={style.addPost}>ADD POST</button>
            </div>
        </form>
    );
};

const NewPostReduxForm = reduxForm({ form: "newPost" })(NewPostForm);

export default UserPosts;
