import { usersAPI } from "../api/api";
import { stateChanged } from "../components/utilits/objHelper/stateChanged";

const FOLLOW = "userSearch/FOLLOW";
const UNFOLLOW = "userSearch/UNFOLLOW";
const SET_USERS = "userSearch/SET-USERS";
const SET_CURRENT_PAGE = "userSearch/SET-CURRENT-PAGE";
const SET_TOTAL_USER_SIZE = "userSearch/SET-TOTAL-USER-SIZE";
const SET_IS_LOADED = "userSearch/SET-IS-LOADED";
const IS_FOLLOWING_IN_PROGRESS = "userSearch/IS-FOLLOWIN_IS_PROGRESS";

let initialState = {
    users: [],
    currentPage: 1,
    countSize: 50,
    totalUserSize: 0,
    isLoaded: true,
    isFollowingInProgress: [],
};

const userSearchReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                users: stateChanged(state.users, action.userId, "id" , {followed: true})
            };
        case UNFOLLOW:
            return {
                ...state,
                users: stateChanged(state.users, action.userId, "id" , {followed: false})
            };
        case SET_USERS:
            return {
                ...state,
                users: [...action.users],
            };
        case SET_CURRENT_PAGE:
            return {
                ...state,
                currentPage: action.currentPage,
            };
        case SET_TOTAL_USER_SIZE:
            return {
                ...state,
                totalUserSize: action.totalUserSize,
            };
        case SET_IS_LOADED:
            return {
                ...state,
                isLoaded: action.isLoaded,
            };
        case IS_FOLLOWING_IN_PROGRESS:
            return {
                ...state,
                isFollowingInProgress: action.isLoaded
                    ? [...state.isFollowingInProgress, action.userId]
                    : state.isFollowingInProgress.filter(
                          (id) => id !== action.userId
                      ),
            };
        default:
            return state;
    }
};

export const following = (userId) => ({ type: FOLLOW, userId });
export const unfollowing = (userId) => ({ type: UNFOLLOW, userId });
export const setUsers = (users) => ({ type: SET_USERS, users });
export const setCurrentPage = (currentPage) => ({
    type: SET_CURRENT_PAGE,
    currentPage,
});
export const setUserTotalCount = (totalUserSize) => ({
    type: SET_TOTAL_USER_SIZE,
    totalUserSize,
});
export const setIsLoaded = (isLoaded) => ({ type: SET_IS_LOADED, isLoaded });
export const isFollowedInProgress = (isLoaded, userId) => ({
    type: IS_FOLLOWING_IN_PROGRESS,
    isLoaded,
    userId,
});

export const receiveUsers = (page, pageSize) => async (dispatch) => {
    dispatch(setIsLoaded(true));
    const response = await usersAPI.getUsers(page, pageSize);
    dispatch(setIsLoaded(false));
    dispatch(setUsers(response.items));
    dispatch(setUserTotalCount(response.totalCount));
};


const followUnfollow = async (dispatch, id, apiMethod, actionCreator) => {
    dispatch(isFollowedInProgress(true, id));
    const response = await apiMethod(id);
    if (response.resultCode === 0) {
        dispatch(actionCreator(id));
    }
    dispatch(isFollowedInProgress(false, id));
}

export const follow = (id) => async (dispatch) => {
    followUnfollow(dispatch, id, usersAPI.follow.bind(usersAPI), following);
};

export const unfollow = (id) => async (dispatch) => {
    followUnfollow(dispatch, id, usersAPI.unfollow.bind(usersAPI), unfollowing);
};

export default userSearchReducer;
