import style from "./ProfilePage.module.css";
import ProfileInfo from "./ProfileInfo/ProfileInfo";
import UserPosts from "./UserPosts/UserPosts";
import Preloader from "../common/preloader/Preloader";

const ProfilePage = ({profilePage, status, uppdateUserStatus, addNewPost}) => {
    return profilePage.profile?(
        <div className={style.myPage}>
            <div className={style.profile}>
                <ProfileInfo profile={profilePage.profile} status={status} uppdateUserStatus={uppdateUserStatus} />
            </div>
            <div className={style.profile}>
                <UserPosts userPosts={profilePage.userPosts} addNewPost={addNewPost} />
            </div>
        </div>
    ):(<Preloader />);
};

export default ProfilePage;
